import { useState, useEffect } from "react";

// Á±ªÂûãÂÆö‰πâ
interface Feed {
  id: string;
  title: string;
  url: string;
  category: string;
  type?: "article" | "video" | "audio" | "image" | "notification";
}

interface Category {
  name: string;
  feeds: Feed[];
  collapsed: boolean;
}

interface Article {
  title: string;
  link: string;
  pubDate: string;
  contentSnippet: string;
  content: string;
  thumbnail?: string;
}

// Cloudflare Workers CORS ‰ª£ÁêÜ
const CORS_PROXY =
  "https://rss-cors-proxy.houzixiashanxiedaima.workers.dev/?url=";

// ÊµèËßàÂô®ÂÖºÂÆπÁöÑ RSS/Atom Ëß£ÊûêÂáΩÊï∞
const parseRSSFeed = async (url: string): Promise<Article[]> => {
  const response = await fetch(url);
  const text = await response.text();
  const parser = new DOMParser();
  const xml = parser.parseFromString(text, "text/xml");

  // Ê£ÄÊü•ÊòØÂê¶ÊòØÊúâÊïàÁöÑ XML
  const parserError = xml.querySelector("parsererror");
  if (parserError) {
    throw new Error("XML Ëß£ÊûêÂ§±Ë¥•");
  }

  const articles: Article[] = [];

  // Ê£ÄÊµãÊòØ RSS ËøòÊòØ Atom
  const isAtom = xml.querySelector("feed") !== null;

  if (isAtom) {
    // Atom feed
    const entries = xml.querySelectorAll("entry");
    entries.forEach((entry) => {
      const title = entry.querySelector("title")?.textContent || "Êó†Ê†áÈ¢ò";
      const link = entry.querySelector("link")?.getAttribute("href") || "";
      const published =
        entry.querySelector("published")?.textContent ||
        entry.querySelector("updated")?.textContent ||
        "";
      const summary = entry.querySelector("summary")?.textContent || "";
      const content =
        entry.querySelector("content")?.textContent ||
        entry.querySelector("summary")?.textContent ||
        "Êó†ÂÜÖÂÆπ";

      articles.push({
        title,
        link,
        pubDate: published,
        contentSnippet: summary.substring(0, 200),
        content,
      });
    });
  } else {
    // RSS feed
    const items = xml.querySelectorAll("item");
    items.forEach((item) => {
      const title = item.querySelector("title")?.textContent || "Êó†Ê†áÈ¢ò";
      const link = item.querySelector("link")?.textContent || "";
      const pubDate = item.querySelector("pubDate")?.textContent || "";
      const description = item.querySelector("description")?.textContent || "";
      const contentEncoded =
        item.querySelector("encoded")?.textContent ||
        item.querySelector("content\\:encoded")?.textContent ||
        "";
      const content = contentEncoded || description || "Êó†ÂÜÖÂÆπ";

      // ÊèêÂèñÁº©Áï•Âõæ
      const mediaContent = item.querySelector("media\\:content, content");
      const mediaThumbnail = item.querySelector("media\\:thumbnail, thumbnail");
      const enclosure = item.querySelector("enclosure");

      let thumbnail: string | undefined;
      if (mediaThumbnail) {
        thumbnail = mediaThumbnail.getAttribute("url") || undefined;
      } else if (mediaContent) {
        thumbnail = mediaContent.getAttribute("url") || undefined;
      } else if (
        enclosure &&
        enclosure.getAttribute("type")?.startsWith("image/")
      ) {
        thumbnail = enclosure.getAttribute("url") || undefined;
      }

      articles.push({
        title,
        link,
        pubDate,
        contentSnippet: description.substring(0, 200),
        content,
        thumbnail,
      });
    });
  }

  return articles;
};

export default function RSSReader() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [selectedFeed, setSelectedFeed] = useState<Feed | null>(null);
  const [articles, setArticles] = useState<Article[]>([]);
  const [selectedArticle, setSelectedArticle] = useState<Article | null>(null);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");

  // Ê†πÊçÆ URL ÂíåÂàÜÁ±ªÊé®Êñ≠ÂÜÖÂÆπÁ±ªÂûã
  const inferFeedType = (
    url: string,
    category: string,
  ): "article" | "video" | "audio" | "image" | "notification" => {
    // Ê†πÊçÆÂàÜÁ±ªÂà§Êñ≠
    if (category === "Articles" || category === "SocialMedia") return "article";
    if (category === "Videos") return "video";
    if (category === "Audios") return "audio";
    if (category === "Pictures") return "image";
    if (category === "Notifications") return "notification";

    // Ê†πÊçÆ URL Âà§Êñ≠
    if (url.includes("bilibili/user/video")) return "video";
    if (url.includes("xiaoyuzhou/podcast")) return "audio";
    if (url.includes("telegram/channel") && category === "Pictures")
      return "image";
    if (url.includes("github.com") && url.includes("releases.atom"))
      return "notification";

    return "article"; // ÈªòËÆ§
  };

  // Ëß£Êûê OPMLÔºà‰øùÁïôÂàÜÁ±ª‰ø°ÊÅØÔºâ
  const parseOPMLWithCategories = (xml: string): Category[] => {
    const parser = new DOMParser();
    const doc = parser.parseFromString(xml, "text/xml");

    const categoriesMap = new Map<string, Feed[]>();
    const topLevelOutlines = doc.querySelectorAll("body > outline");

    topLevelOutlines.forEach((categoryOutline) => {
      const categoryName = categoryOutline.getAttribute("text") || "Êú™ÂàÜÁ±ª";

      // ÈÄíÂΩíÂ§ÑÁêÜÂ≠êÂàÜÁ±ªÂíåËÆ¢ÈòÖÊ∫ê
      const processOutline = (outline: Element, parentCategory: string) => {
        const xmlUrl = outline.getAttribute("xmlUrl");

        if (xmlUrl) {
          // ËøôÊòØ‰∏Ä‰∏™ËÆ¢ÈòÖÊ∫ê
          if (!categoriesMap.has(parentCategory)) {
            categoriesMap.set(parentCategory, []);
          }

          const feeds = categoriesMap.get(parentCategory)!;
          feeds.push({
            id: `${Date.now()}_${feeds.length}`,
            title:
              outline.getAttribute("title") ||
              outline.getAttribute("text") ||
              "Êú™ÂëΩÂêç",
            url: xmlUrl,
            category: parentCategory,
            type: inferFeedType(xmlUrl, parentCategory),
          });
        } else {
          // ËøôÊòØ‰∏Ä‰∏™Â≠êÂàÜÁ±ª
          const subCategory = outline.getAttribute("text") || "Êú™ÂëΩÂêç";
          const subOutlines = outline.querySelectorAll(":scope > outline");
          subOutlines.forEach((sub) =>
            processOutline(sub, `${parentCategory} / ${subCategory}`),
          );
        }
      };

      const outlines = categoryOutline.querySelectorAll(":scope > outline");
      outlines.forEach((outline) => processOutline(outline, categoryName));
    });

    // ËΩ¨Êç¢‰∏∫Êï∞ÁªÑ
    const categories: Category[] = [];
    categoriesMap.forEach((feeds, name) => {
      categories.push({
        name,
        feeds,
        collapsed: false,
      });
    });

    return categories;
  };

  // Ëá™Âä®Âä†ËΩΩÈ°πÁõÆ‰∏≠ÁöÑ OPML Êñá‰ª∂
  useEffect(() => {
    const loadOPML = async () => {
      try {
        const response = await fetch("/follow.opml");
        const xml = await response.text();
        const parsedCategories = parseOPMLWithCategories(xml);
        setCategories(parsedCategories);

        const totalFeeds = parsedCategories.reduce(
          (sum, cat) => sum + cat.feeds.length,
          0,
        );
        console.log(
          `‚úÖ Ëá™Âä®Âä†ËΩΩ ${totalFeeds} ‰∏™ËÆ¢ÈòÖÊ∫êÔºåÂàÜ‰∏∫ ${parsedCategories.length} ‰∏™ÂàÜÁ±ª`,
        );
      } catch (error) {
        console.error("‚ùå Ëá™Âä®Âä†ËΩΩ OPML Â§±Ë¥•:", error);
      }
    };

    loadOPML();
  }, []);

  // ÂØºÂÖ• OPMLÔºàÊâãÂä®ÂØºÂÖ•ÔºåÂèØÈÄâÔºâ
  const handleOPMLImport = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      const xml = event.target?.result as string;
      const parsedCategories = parseOPMLWithCategories(xml);
      setCategories(parsedCategories);

      const totalFeeds = parsedCategories.reduce(
        (sum, cat) => sum + cat.feeds.length,
        0,
      );
      alert(
        `ÊàêÂäüÂØºÂÖ• ${totalFeeds} ‰∏™ËÆ¢ÈòÖÊ∫êÔºåÂàÜ‰∏∫ ${parsedCategories.length} ‰∏™ÂàÜÁ±ªÔºÅ`,
      );
    };
    reader.readAsText(file);
  };

  // ÂàáÊç¢ÂàÜÁ±ªÊäòÂè†Áä∂ÊÄÅ
  const toggleCategory = (categoryName: string) => {
    setCategories((prev) =>
      prev.map((cat) =>
        cat.name === categoryName ? { ...cat, collapsed: !cat.collapsed } : cat,
      ),
    );
  };

  // Ëé∑Âèñ RSS ÊñáÁ´†
  const fetchFeed = async (feed: Feed) => {
    setLoading(true);
    setSelectedFeed(feed);
    setArticles([]);
    setSelectedArticle(null);

    try {
      const proxyUrl = CORS_PROXY + encodeURIComponent(feed.url);
      const newArticles = await parseRSSFeed(proxyUrl);

      setArticles(newArticles);
      if (newArticles.length > 0) {
        setSelectedArticle(newArticles[0]);
      }
    } catch (error: any) {
      console.error("Ëé∑Âèñ RSS Â§±Ë¥•:", error);
      alert("Ëé∑Âèñ RSS Â§±Ë¥•: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  // ÂàÜÁ±ªÂõæÊ†á
  const getCategoryIcon = (categoryName: string) => {
    const icons: Record<string, string> = {
      Articles: "üì∞",
      Videos: "üé¨",
      Audios: "üéß",
      Pictures: "üñºÔ∏è",
      SocialMedia: "üí¨",
      Notifications: "üîî",
    };
    return icons[categoryName] || "üìÅ";
  };

  // ÂÜÖÂÆπÁ±ªÂûãÂõæÊ†á
  const getTypeIcon = (type?: string) => {
    const icons: Record<string, string> = {
      article: "üìÑ",
      video: "‚ñ∂Ô∏è",
      audio: "üéµ",
      image: "üñºÔ∏è",
      notification: "üîî",
    };
    return icons[type || "article"];
  };

  // ËøáÊª§ÂàÜÁ±ªÔºàÊ†πÊçÆÊêúÁ¥¢Ôºâ
  const filteredCategories = categories
    .map((cat) => ({
      ...cat,
      feeds: cat.feeds.filter((feed) =>
        feed.title.toLowerCase().includes(searchQuery.toLowerCase()),
      ),
    }))
    .filter((cat) => cat.feeds.length > 0);

  // ÂèãÂ•ΩÊòæÁ§∫Áõ∏ÂØπÊó∂Èó¥
  const formatRelativeTime = (dateStr: string) => {
    if (!dateStr) return "Êú™Áü•Êó∂Èó¥";
    const date = new Date(dateStr);
    if (isNaN(date.getTime())) return "Êú™Áü•Êó∂Èó¥";

    const diffMs = Date.now() - date.getTime();
    const diffSec = Math.floor(diffMs / 1000);
    const diffMin = Math.floor(diffSec / 60);
    const diffHr = Math.floor(diffMin / 60);
    const diffDay = Math.floor(diffHr / 24);

    if (diffSec < 60) return "ÂàöÂàö";
    if (diffMin < 60) return `${diffMin} ÂàÜÈíüÂâç`;
    if (diffHr < 24) return `${diffHr} Â∞èÊó∂Ââç`;
    if (diffDay < 30) return `${diffDay} Â§©Ââç`;
    return date.toLocaleDateString("zh-CN");
  };

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-900">
      {/* Â∑¶Ê†è: ÂàÜÁ±ª + ËÆ¢ÈòÖÊ∫êÂàóË°® */}
      <aside className="w-72 bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700 overflow-y-auto flex flex-col">
        {/* È°∂ÈÉ®Âå∫Âüü */}
        <div className="p-4 border-b border-gray-200 dark:border-gray-700">
          <h2 className="text-lg font-bold mb-3 text-gray-900 dark:text-gray-100">
            RSS ÈòÖËØªÂô®
          </h2>

          {/* ÊâãÂä®ÂØºÂÖ•ÊåâÈíÆÔºàÂèØÈÄâÔºâ */}
          {categories.length > 0 && (
            <label className="block px-4 py-2 text-xs bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 text-center rounded-lg cursor-pointer hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors mb-3">
              üîÑ ÈáçÊñ∞ÂØºÂÖ• OPML
              <input
                type="file"
                accept=".opml,.xml"
                onChange={handleOPMLImport}
                className="hidden"
              />
            </label>
          )}

          {/* ÊêúÁ¥¢Ê°Ü */}
          {categories.length > 0 && (
            <input
              type="text"
              placeholder="üîç ÊêúÁ¥¢ËÆ¢ÈòÖÊ∫ê..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full mt-3 px-3 py-2 text-sm border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          )}
        </div>

        {/* ÂàÜÁ±ªÂàóË°® */}
        <div className="flex-1 overflow-y-auto">
          {filteredCategories.length === 0 ? (
            <div className="p-6 text-center text-gray-500 dark:text-gray-400">
              {categories.length === 0 ? "Ê≠£Âú®Âä†ËΩΩËÆ¢ÈòÖÊ∫ê..." : "Êó†ÂåπÈÖçÁöÑËÆ¢ÈòÖÊ∫ê"}
            </div>
          ) : (
            filteredCategories.map((category) => (
              <div
                key={category.name}
                className="border-b border-gray-200 dark:border-gray-700"
              >
                {/* ÂàÜÁ±ªÊ†áÈ¢òÔºàÂèØÊäòÂè†Ôºâ*/}
                <button
                  onClick={() => toggleCategory(category.name)}
                  className="w-full px-4 py-3 flex items-center justify-between bg-gray-50 dark:bg-gray-750 hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
                >
                  <div className="flex items-center gap-2">
                    <span className="text-lg">
                      {getCategoryIcon(category.name)}
                    </span>
                    <span className="font-medium text-gray-900 dark:text-gray-100">
                      {category.name}
                    </span>
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      ({category.feeds.length})
                    </span>
                  </div>

                  {/* ÊäòÂè†ÁÆ≠Â§¥ */}
                  <svg
                    className={`w-4 h-4 text-gray-500 transition-transform ${
                      category.collapsed ? "" : "rotate-90"
                    }`}
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 5l7 7-7 7"
                    />
                  </svg>
                </button>

                {/* ËÆ¢ÈòÖÊ∫êÂàóË°® */}
                {!category.collapsed && (
                  <div className="bg-white dark:bg-gray-800">
                    {category.feeds.map((feed) => (
                      <button
                        key={feed.id}
                        onClick={() => fetchFeed(feed)}
                        className={`w-full text-left px-6 py-3 hover:bg-gray-50 dark:hover:bg-gray-700 flex items-center gap-2 transition-colors border-l-4 ${
                          selectedFeed?.id === feed.id
                            ? "bg-blue-50 dark:bg-blue-900/20 border-blue-500"
                            : "border-transparent"
                        }`}
                      >
                        {/* ÂÜÖÂÆπÁ±ªÂûãÊ†áËÆ∞ */}
                        <span className="text-sm flex-shrink-0">
                          {getTypeIcon(feed.type)}
                        </span>
                        <span className="truncate text-sm text-gray-900 dark:text-gray-100">
                          {feed.title}
                        </span>
                      </button>
                    ))}
                  </div>
                )}
              </div>
            ))
          )}
        </div>
      </aside>

      {/* ‰∏≠Ê†è: ÊñáÁ´†ÂàóË°® */}
      <div className="w-80 bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700 overflow-y-auto flex flex-col">
        {selectedFeed ? (
          <>
            <div className="p-4 border-b border-gray-200 dark:border-gray-700">
              <div className="flex items-center gap-2 mb-1">
                <span className="text-lg">
                  {getTypeIcon(selectedFeed.type)}
                </span>
                <h2 className="text-lg font-bold text-gray-900 dark:text-gray-100">
                  {selectedFeed.title}
                </h2>
              </div>
              <p className="text-sm text-gray-500 dark:text-gray-400">
                {articles.length} ÁØáÊñáÁ´†
              </p>
            </div>

            {loading ? (
              <div className="flex-1 flex items-center justify-center">
                <div className="text-center">
                  <div className="inline-block animate-spin rounded-full h-8 w-8 border-4 border-blue-500 border-t-transparent"></div>
                  <p className="mt-2 text-sm text-gray-500 dark:text-gray-400">
                    Âä†ËΩΩ‰∏≠...
                  </p>
                </div>
              </div>
            ) : (
              <div className="flex-1 overflow-y-auto p-3 space-y-3">
                {articles.map((article, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedArticle(article)}
                    className="w-full text-left"
                  >
                    <div
                      className={`flex gap-3 items-center rounded-xl border transition-colors bg-gray-50 border-gray-200 dark:bg-gray-800/80 dark:border-gray-700 hover:border-blue-400 dark:hover:border-blue-400 ${
                        selectedArticle?.link === article.link
                          ? "ring-2 ring-blue-400/50"
                          : ""
                      }`}
                    >
                      <div className="flex-1 min-w-0 px-4 py-3">
                        <div className="flex items-center gap-2 text-xs text-gray-500 dark:text-gray-400 mb-1">
                          <span className="font-semibold text-gray-900 dark:text-gray-100">
                            {selectedFeed?.title}
                          </span>
                          <span className="h-1 w-1 rounded-full bg-amber-500 inline-block"></span>
                          <span>{formatRelativeTime(article.pubDate)}</span>
                        </div>
                        <h3 className="text-base font-semibold mb-1 line-clamp-2 text-gray-900 dark:text-gray-100">
                          {article.title}
                        </h3>
                        <p className="text-sm leading-relaxed text-gray-600 dark:text-gray-300 line-clamp-2">
                          {article.contentSnippet}
                        </p>
                      </div>

                      {article.thumbnail && (
                        <div className="relative w-24 h-24 mr-3 flex-shrink-0 overflow-hidden rounded-xl bg-gray-100 dark:bg-gray-700">
                          <img
                            src={article.thumbnail}
                            alt=""
                            loading="lazy"
                            className="w-full h-full object-cover"
                          />
                        </div>
                      )}
                    </div>
                  </button>
                ))}
              </div>
            )}
          </>
        ) : (
          <div className="flex-1 flex items-center justify-center text-gray-500 dark:text-gray-400">
            <div className="text-center">
              <span className="text-4xl mb-2 block">üì∞</span>
              <p>ÈÄâÊã©‰∏Ä‰∏™ËÆ¢ÈòÖÊ∫êÂºÄÂßãÈòÖËØª</p>
            </div>
          </div>
        )}
      </div>

      {/* Âè≥Ê†è: ÊñáÁ´†ÂÜÖÂÆπ */}
      <main className="flex-1 bg-white dark:bg-gray-800 overflow-y-auto">
        {selectedArticle ? (
          <article className="w-full h-full px-12 py-8 max-w-none">
            <h1 className="text-3xl font-bold mb-4 text-gray-900 dark:text-gray-100">
              {selectedArticle.title}
            </h1>

            <div className="flex items-center gap-4 text-sm text-gray-500 dark:text-gray-400 mb-6 pb-6 border-b border-gray-200 dark:border-gray-700">
              <span>
                {selectedArticle.pubDate
                  ? new Date(selectedArticle.pubDate).toLocaleString("zh-CN")
                  : "Êú™Áü•Êó∂Èó¥"}
              </span>
              <a
                href={selectedArticle.link}
                target="_blank"
                rel="noopener noreferrer"
                className="text-blue-500 hover:text-blue-600 dark:text-blue-400 dark:hover:text-blue-300 hover:underline"
              >
                Êü•ÁúãÂéüÊñá ‚Üí
              </a>
            </div>

            <div
              className="prose prose-lg dark:prose-invert max-w-none
                prose-headings:text-gray-900 dark:prose-headings:text-gray-100
                prose-p:text-gray-700 dark:prose-p:text-gray-300
                prose-a:text-blue-500 dark:prose-a:text-blue-400
                prose-strong:text-gray-900 dark:prose-strong:text-gray-100
                prose-code:text-gray-900 dark:prose-code:text-gray-100
                prose-pre:bg-gray-100 dark:prose-pre:bg-gray-900
                prose-img:rounded-lg"
              dangerouslySetInnerHTML={{ __html: selectedArticle.content }}
            />
          </article>
        ) : (
          <div className="flex items-center justify-center h-full text-gray-500 dark:text-gray-400">
            <div className="text-center">
              <span className="text-4xl mb-2 block">üìñ</span>
              <p>ÈÄâÊã©‰∏ÄÁØáÊñáÁ´†ÂºÄÂßãÈòÖËØª</p>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
